  grid-column: span 4;
  border: 1px solid #000;
}

.block2 {
  grid-row: span 3;
  border: 1px solid #000;
}

.block3 {
  grid-area: span 2/span 2;
  border: 1px solid #000;
}

.block4 {
  grid-area: span 2/span 2;
  border: 1px solid #000;
}

.level__icon {
  display: block;
  width: 24px;
  height: 24px;
}

